<template>
  <div id="app">
    <header>
		<span>SkillMithra Job Feeder</span>
    </header>
    <main>
		<div v-if="loading">
		  Loading.....
		</div>
		
		<div class="wrapper card-columns">
			<div v-for="job in jobs">
				<div class="card" style="width:98%;height:auto">
				  <div class="card-body">
					<h4 class="card-title">{{ job.title }}</h4>
					<p style="text-align:right"><small>{{ job.companyname }}</small></p>
					<hr>
					<div v-if="job.jd">
						<p class="card-text">{{ job.jd }}</p>
						<hr>
					</div>
					<ul>
					<div v-if="job.skills">
						<li><img src="./assets/skills.png" style="height:1em" alt="Requirements:"> {{ job.skills }}</li>
					</div>
					<div v-if="job.location">
						<li><img src="./assets/location.png" style="height:1em" alt="Location:"> {{ job.location }}</li>
					</div>
					<div v-if="job.salary">
						<li><img src="./assets/salary.png" style="height:1em" alt="Salary:"> {{ job.salary }}</li>
					</div>
					<div v-if="job.type">
						<li><b>Type:</b> {{ job.type }}</li>
					</div>
					<div v-if="job.experience">
						<li><img src="./assets/experience.png" style="height:1em" alt="Experience:"> {{ job.experience }}</li>
					</div>
					<div v-if="job.startdate">
						<li><b>Start Date:</b> {{ job.startdate }}</li>
					</div>
					<div v-if="job.enddate">
						<li><b>End Date:</b> {{ job.enddate }}</li>
					</div>
					<!--
					<div v-if="job.created">
						<li>Posted {{ job.created }}</li>
					</div>
					-->
					</ul>
					<hr>
					<a :href="job.applylink" target="_blank" class="btn btn-primary form-control" style="text-align:center">Apply</a>
					<hr>
					<div style="text-align:right"><small>Source: {{ job.source }}</small></div>
				  </div>
				</div>
			</div>
		</div>
    </main>
  </div>
</template>

<script>
import axios from 'axios';
  
export default {
  name: 'jobfeeder',
  data () {
    return {
      jobs: [],
      loading: false
    }
  }, 
  methods: {
    getJobs: function () {
      this.loading = true;
      axios.get("https://nthacks.github.io/JobFeeder/test.json")
      .then((response)  =>  {
        this.loading = false;
        this.jobs = response.data.data;
      }, (error)  =>  {
        this.loading = false;
      })
    }
  },
  beforeMount(){
    this.getJobs();
 },
}
</script>

<style>
body {
  margin: 0;
  text-align:center;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
.card {
	text-align: left;
	-webkit-box-shadow: 0px 0px 5px 0px rgba(0,0,0,1);
	-moz-box-shadow: 0px 0px 5px 0px rgba(0,0,0,1);
	box-shadow: 0px 0px 5px 0px rgba(0,0,0,1);
	margin:1%;
	padding-bottom:1px;
}
main {
  text-align: inherit;
  margin-top: 40px;
}

header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  color: #ffffff;
}

header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}
</style>



